// Slice Actions
import globalSliceActions from './globalSliceActions'

// Slice
const globalSlice = (set, get) => ({
  // Global State
  viewMode: {
    isEditor: false,
    isDashboard: false,
    isWizard: false
  },
  os: navigator.userAgent.indexOf('Win') !== -1 ? 'Win' : navigator.userAgent.indexOf('Mac') !== -1 ? 'MacOS' : null,
  overlay: {
    object: null,
    show: false,
    disableBgClose: false
  },
  viewport: 'lg',
  dimensions: null,

  ...globalSliceActions(set, get),
})

export default globalSlice
